import { getData } from './controller';
import { checkController } from './controller';
import { uncheckController } from './controller';
import { renderallController } from './controller';
import { renderActiveController } from './controller';
import { renderCompletedController } from './controller';
import { renderclearCompletedController } from './controller';

const input = document.querySelector('.form_input');
const form = document.querySelector('.form');
const box = document.querySelector('.box');
const all = document.querySelector('.item-all');
const active = document.querySelector('.item-active');
const completed = document.querySelector('.item-completed');
const clearCompleted = document.querySelector('.item-clear-completed');
const firstContainer = document.querySelector('.first-container');
const count = document.querySelector('.count');
const lastItem = document.querySelector('.last');

const font = document.querySelectorAll('.font');
let work = '';
export const formEventListner = function () {
  form.addEventListener('submit', function (e) {
    e.preventDefault();
    work = input.value;
    work = work.replaceAll(' ', '-');
    input.value = '';
    getData(work);
    // renderWork(work);
  });
};

const markupGenerator = function (work) {
  const html = `
  <div class="new-item">

      
        <input class = 'check' type="checkbox" id="${work.work}"  />
        <label class = '${work.work}' for="${work.work}" ></label>
        <h1 class = ' heading ${work.active ? '' : 'addLine'}'>${work.work}</h1>
      

    <svg class="cross-svg ${
      work.work
    }" xmlns="http://www.w3.org/2000/svg" width="18" height="18"><path fill="#494C6B" fill-rule="evenodd" d="M16.97 0l.708.707L9.546 8.84l8.132 8.132-.707.707-8.132-8.132-8.132 8.132L0 16.97l8.132-8.132L0 .707.707 0 8.84 8.132 16.971 0z"/></svg>

</div>
<div><span class="hr hr-light"></span></div>
`;

  box.insertAdjacentHTML('afterbegin', html);
};

export const clear = function () {
  const selector = firstContainer.querySelectorAll('.box >  div:not(.last)');

  if (selector.length !== 0) {
    selector.forEach(element => element.remove());
  }
};

export const renderWork = function (workArr) {
  workArr.forEach(work => markupGenerator(work));
};
let a = 1;
box.addEventListener('click', function (e) {
  if (a === 1) {
    if (e.target.closest('.check')?.checked) {
      checkController();
      a++;
    }
  } else {
    const findtoUncheck = e.target.classList[0];
    if (findtoUncheck === 'font') return;

    uncheckController(findtoUncheck);

    a = 1;
  }
});

export const makesvgWork = function (workArray) {
  const svg = document.querySelectorAll('.cross-svg');

  svg.forEach((el, i) =>
    el.addEventListener('click', function (e) {
      // try filter to find gym using index on array
      // if (Array.from(e.target.classList)[1] === workArr.) {
      //   workArr.splice(i, 1);
      //   console.log(el);
      // }
      const index = workArray.findIndex(
        el => Array.from(e.target?.classList)[1] === el.work
      );

      if (!Array.from(e.target.classList)[1]) return;
      // if (workArray.length === 1) workArray = [];
      // else {
      //   workArray.splice(index, 1);
      // }
      workArray.splice(index, 1);

      clear();
      renderWork(workArray);
      updateCount(workArray);
      makesvgWork(workArray);
    })
  );
};

export const updateCount = function (workArray) {
  const workCount = workArray.filter(ele => ele.active === true);
  count.textContent = workCount.length;
};

all.addEventListener('click', function () {
  renderallController();
});

active.addEventListener('click', function () {
  renderActiveController();
});

completed.addEventListener('click', function () {
  renderCompletedController();
});

clearCompleted.addEventListener('click', function () {
  renderclearCompletedController();
});

lastItem.addEventListener('click', function (e) {
  const clicked = e.target.closest('.font');

  if (!clicked) return;

  font.forEach(t => t.classList.remove('active'));
  clicked.classList.add('active');
});
