import { async } from 'regenerator-runtime';

import { formEventListner } from './view';
import { renderWork } from './view';
import checkview from './checkview';
import { clear } from './view';
import Theme from './theme';
import { makesvgWork } from './view';
import { updateCount } from './view';
formEventListner();
let workArray = [];
class Work {
  constructor(active = true, work, id) {
    this.id = id;
    (this.active = active), (this.work = work);
  }
}

const handleClick = function (checkNodlist, workArr) {
  const nodeArray = Array.from(checkNodlist);

  // console.log(checkNodlist);
  nodeArray.forEach((element, i) => {
    if (element.checked) {
      workArr[i].active = false;
    }
  });

  updateCount(workArray);
};

export const checkController = function () {
  const checkNode = checkview.checkMethod();
  handleClick(checkNode, workArray);
};

export const uncheckController = function (findtoUncheck) {
  const index = workArray.findIndex(work => work.work === findtoUncheck);

  workArray[index].active
    ? (workArray[index].active = false)
    : (workArray[index].active = true);

  updateCount(workArray);
};

export const renderallController = function () {
  clear();
  renderWork(workArray);
};

export const renderActiveController = function () {
  const activeWorkArray = workArray.filter(work => {
    if (work.active) {
      return work;
    }
  });
  clear();
  renderWork(activeWorkArray);
};

export const renderCompletedController = function () {
  const completedWorkArray = workArray.filter(work => {
    if (!work.active) return work;
  });
  clear();
  renderWork(completedWorkArray);
};

export const renderclearCompletedController = function () {
  const completedWorkArray = workArray.filter((work, i) => {
    if (!work.active) workArray.splice(i, 1);
  });
  clear();
  renderWork(workArray);
};
let i = 1;

const createObject = function (todoItem) {
  const workobj = new Work(true, todoItem, i);
  i++;
  workArray.push(workobj);

  clear();
  renderWork(workArray);
  updateCount(workArray);
  makesvgWork(workArray);

  // storing todoitem to ocal storage
  setLocalStorage();
};

export const getData = function (todoItem) {
  createObject(todoItem);
};

// for changing the svg from light to
let a = 1;
const themeController = function () {
  if (a === 1) {
    Theme.darktheme();
    a = 2;
  } else {
    Theme.lighttheme();
    a = 1;
  }
};

const setLocalStorage = function () {
  localStorage.setItem('workouts', JSON.stringify(workArray));
};

const getLocalStorage = function () {
  const data = JSON.parse(localStorage.getItem('workouts'));
  if (!data) return;

  data.forEach(work => createObject(work.work));
  //console.log(data[0].work);
  // clear();
  // renderWork(workArray);
};

const init = function () {
  Theme.addThemeHandler(themeController);
  getLocalStorage();
};
init();
